import React from 'react';
import axios from 'axios';
import qs from 'qs';
import QRCode from 'qrcode';

import {
  GlobalStyles, AppBar, Link, Typography, Button, Toolbar, Container, TextField, Box, Paper,
} from '@mui/material';

import '../css/root.css';

export default function Root() {
  // const preventDefault = (event) => event.preventDefault();
  const login = () => { console.log('login'); };

  const CopyURL = () => {
    navigator.clipboard.writeText(document.querySelector('#shortURL').text);
  };
  const QRCodeGen = (url) => {
    QRCode.toCanvas(document.getElementById('qrcode'), url, (error) => {
      if (error) console.error(error);
      console.log('success!');
    });
  };
  const getShortenURL = () => {
    const data = { url: document.querySelector('#url').value };
    const qsdata = qs.stringify(data);
    document.querySelector('#shortURL').text = 'Process...';
    axios
      .post('https://shorteningurl-eh4konhfta-de.a.run.app/shorten', qsdata)
      // .post('http://127.0.0.1:3000/shorten', qsdata)
      .then((response) => {
        console.log(response.data.clickTime);
        console.log(response.data.shortUrl);
        if (response.data.clickTime === undefined) {
          document.querySelector('#shortURL').text = 'URL is Error,Please Re-check the URL';
          document.querySelector('#clickTime').innerText = 0;
          QRCodeGen('https://shorteningurl-eh4konhfta-de.a.run.app');
        } else {
          document.querySelector('#shortURL').text = response.data.shortUrl;
          document.querySelector('#shortURL').href = response.data.shortUrl;
          document.querySelector('#clickTime').innerText = response.data.clickTime;
          QRCodeGen(response.data.shortUrl);
        }
      })
      .catch((error) => {
        console.log(error);
      }).finally(() => {
        document.querySelector('.resultPaper').style.visibility = 'visible';
      });
  };

  return (
    <>

      <GlobalStyles styles={{ ul: { margin: 0, padding: 0, listStyle: 'none' } }} />

      <AppBar
        position="static"
        color="default"
        elevation={0}
        sx={{ borderBottom: (theme) => `1px solid ${theme.palette.divider}` }}
      >
        <Toolbar sx={{ flexWrap: 'wrap' }}>
          <Typography variant="h6" color="inherit" noWrap sx={{ flexGrow: 1 }}>
            URL shortener
          </Typography>
          <nav>
            <Link variant="button" color="text.primary" href="./" sx={{ my: 1, mx: 1.5 }}>
              Support
            </Link>
          </nav>
          <Button href="#" variant="outlined" sx={{ my: 1, mx: 1.5 }} onClick={login}>
            Login
          </Button>
        </Toolbar>
      </AppBar>
      <Box height="30%" display="flex" flexDirection="column" textAlign="center">
        <Container disableGutters maxWidth="md" component="main" sx={{ pt: 15, pb: 6 }}>
          <Typography
            component="h1"
            variant="h2"
            align="center"
            color="text.primary"
            gutterBottom
          >
            <div id="description">
              Make your
              {' '}
              <b>URL</b>
              {' '}
              <b>shorter.</b>
            </div>
          </Typography>
          <form className="URLform" noValidate autoComplete="off">
            <TextField name="url" id="url" label="input your URL here!!" fullWidth />
          </form>
          <br />
          <Button variant="contained" color="primary" onClick={getShortenURL}>
            Shortening
          </Button>
        </Container>
      </Box>
      <Box height="50%" width="80wh" display="flex" flexDirection="column" justifyContent="start" textAlign="center">
        <Container disableGutters maxWidth="md" component="main" sx={{ pt: 0, pb: 6 }}>
          <Paper
            square
            sx={{
              p: 2, display: 'flex', flexDirection: 'column', height: '100%', width: '100%',
            }}
            className="resultPaper"
            style={{ visibility: 'hidden' }}
          >

            <Typography
              variant="h2"
              align="center"
              color="text.primary"
              gutterBottom
            >
              Result
            </Typography>
            <Typography
              variant="h5"
              align="center"
              color="text.primary"
              gutterBottom
            >
              <Link
                id="shortURL"
                href="https://v4.mui.com/zh/components/links/#accessibility"
                // onClick={preventDefault}
                target="_blank"
              >
                https://v4.mui.com/zh/components/links/#accessibility
              </Link>
              <br />
              <Button
                variant="contained"
                color="primary"
                sx={{
                  marginTop: '1.5rem',
                }}
                onClick={CopyURL}
              >
                Copy URL
              </Button>
              <br />
              <br />
              clickTime
              <div id="clickTime">
                0
              </div>
              <canvas id="qrcode" style={{ marginTop: '1rem' }} />
            </Typography>

            {/* <img src="https://kaizziizg.github.io/img/qrcode2.png" alt="QRCode" width="30%" style={{ margin: 'auto' }} /> */}
          </Paper>
        </Container>
      </Box>
    </>
  );
}
